import React from 'react';
import { CInputCheckbox } from '@coreui/react';

export interface InputRadioTypes extends CInputCheckbox {
  id: string;
  className?: string;
}

const InputRadioCustom = (props: InputRadioTypes) => {
  const { id, ...rest } = props;

  return (
    <span className="InputRadio-radio">
      <input type="radio" id={id} {...rest} />
      <span />
    </span>
  );
};

export default InputRadioCustom;
