import { APPOINTMENT_STEPS } from 'configs/constants';
import { ActionTypes } from 'types/common';

import {
  APPOINTMENT_SET_STEP,
  APPOINTMENT_UPDATE_QUESTIONS,
  APPOINTMENT_RESET,
  APPOINTMENT_UPDATE_TOPICS,
  APPOINTMENT_UPDATE_APPOINTMENT_DATA,
  APPOINTMENT_UPDATE_STATISTICS,
} from '../actionTypes';

const initialState = {
  step: APPOINTMENT_STEPS.TOPICS,
  topics: [],
  questions: [],
  statistics: {},
  appointmentData: {
    selectedTopic: null,
    adviceRelatives: [],
    surveyAnswers: {},
    scheduleData: {
      selectedOption: '',
      specificDate: '',
      timeStart: '',
      timeEnd: '',
    },
    userInfo: {},
  },
};

const appointmentReducer = (state: any = initialState, action: ActionTypes) => {
  const { type, payload } = action;

  switch (type) {
    case APPOINTMENT_SET_STEP:
      return {
        ...state,
        step: payload,
      };
    case APPOINTMENT_UPDATE_TOPICS: {
      return {
        ...state,
        topics: payload,
      };
    }
    case APPOINTMENT_UPDATE_QUESTIONS: {
      return {
        ...state,
        questions: payload,
      };
    }
    case APPOINTMENT_UPDATE_APPOINTMENT_DATA: {
      return {
        ...state,
        appointmentData: {
          ...state.appointmentData,
          ...payload,
        },
      };
    }
    case APPOINTMENT_UPDATE_STATISTICS: {
      return {
        ...state,
        statistics: payload,
      };
    }
    case APPOINTMENT_RESET: {
      return {
        ...state,
        ...initialState,
      };
    }
    default:
      return state;
  }
};

export default appointmentReducer;
