import React from 'react';
import { CTextarea } from '@coreui/react';

import './InputTextArea.scss';

interface PropTypes extends CTextarea {
  className?: string;
}

const InputTextArea = (props: PropTypes) => {
  const { className = '', rows = 8, ...rest } = props;

  return <textarea className={`InputTextArea-area ${className}`} rows={rows} {...rest} />;
};

export default InputTextArea;
