import { PAID, UNPAID } from './actions';

const initialState = { show: false, message: "" };

export const paymentReducer = (state = initialState, action: { type: string; payload: any }) => {
    switch (action.type) {
        case PAID:
            return { ...initialState };

        case UNPAID:
            return { ...state, show: true, message: action.payload };

        default:
            return state;
    }
};