import { includes } from 'utils';
import { SIGN_IN, SIGN_OUT, REFRESH_TOKEN, UPDATE_PROFILE, ADD_STRIPE_PAYMENT, REMOVE_STRIPE_PAYMENT } from './actions';

const initialState: any = {};

export const authReducer = (state = initialState, action: { type: string; payload: any }) => {
  switch (action.type) {
    case SIGN_IN:
      return { ...action.payload };

    case SIGN_OUT:
      return {};

    case REFRESH_TOKEN:
    case UPDATE_PROFILE:
      return { ...state, ...action.payload };

    case ADD_STRIPE_PAYMENT:
      console.log("ADD_PAYMENT", [...state.stripe_payment, action.payload])
      return { ...state, stripe_payment: [...state.stripe_payment, action.payload] };
    case REMOVE_STRIPE_PAYMENT:

      const topic = action.payload;
      console.log("REMOVE_PAYMENT", state.stripe_payment.filter((item: any) => item.id !== topic.id))
      return { ...state, stripe_payment: state.stripe_payment.filter((item: any) => item.id !== topic.id) };

    default:
      return state;
  }
};
