import React from 'react';
import { CFormGroup } from '@coreui/react';

import './FormGroup.scss';
import { HBButtonSmall } from 'hbBaseClass';

export interface FormGroupCommonTypes {
  label?: string;
  required?: boolean;
  btnLabel?: string;
  btnLabelOnClick?: Function;
  msgError?: string;
}

const FormGroupCommon = (props: FormGroupCommonTypes & CFormGroup) => {
  const { className = '', label, required, btnLabel, btnLabelOnClick = () => {}, msgError = '', children } = props;

  return (
    <CFormGroup className={`HBFormGroup-container ${className} ${msgError && 'error'}`}>
      <div className="HBFormGroup-lb">
        {label && (
          <span>
            {label}
            {required && '*'}
          </span>
        )}
        {btnLabel && <HBButtonSmall onClick={btnLabelOnClick}>{btnLabel}</HBButtonSmall>}
      </div>
      {children}
      {msgError && <p className="HBFormGroup-error">{msgError}</p>}
    </CFormGroup>
  );
};

export default FormGroupCommon;
