import React from 'react';

import InputRadio, { InputRadioTypes } from './InputRadio';

interface Option extends InputRadioTypes {
  label: string;
  value: string | number;
  icon?: string;
  checked?: boolean;
}

interface PropTypes {
  className?: string;
  options: Option[];
  [key: string]: any;
}

const ListInputRadios = (props: PropTypes) => {
  const { className = '', options = [], value, onChange = () => {}, ...rest } = props;

  return (
    <div className={`ListInputRadios-container ${className}`} {...rest}>
      {options.map((option, index) => (
        <InputRadio key={index} {...option} checked={`${value}` === `${option.value}`} onChange={onChange} />
      ))}
    </div>
  );
};

export default ListInputRadios;
