import { ItemActivityType } from 'config';
import { ACTIVITY_TYPES } from 'configs/constants';

const { GENERAL, ARTICLE_OF_WEEK, NEW_VIDEO, UPCOMING_APPOINTMENT, TRAINER, NEW_REPORT } = ACTIVITY_TYPES;

/**
 * Handle when click an item notification
 * @param history define from useHistory
 * @param activityItem body json from socket
 */
export const handleClickActivity = (history: any, activityItem: ItemActivityType) => {
  const { body_json = {} } = activityItem;

  switch (activityItem.type) {
    case ARTICLE_OF_WEEK:
      window.open(body_json.source);
      break;

    case NEW_VIDEO:
      history.push(`/mobility/watch?video=${body_json.id}`);
      break;

    default:
      history.push('/activity');
      break;
  }
};
