import { CFormGroup, CInput, CInvalidFeedback, CLabel } from '@coreui/react';
import React from 'react';

/**
 * render the eye and eye-slash icon when password is shown or not
 * @param showPassword
 * @param onClick handle when user click this icon, change show status
 */
export const renderIconPassword = (showPassword: boolean, onClick: () => void) => (
  <div className="d-flex justify-content-center align-items-center cursor-pointer px-3">
    <i
      className={`fas ${showPassword ? 'fa-eye-slash' : 'fa-eye'}`}
      onClick={onClick}
      style={{ width: 18, height: 18, color: 'gray' }}
    />
  </div>
);

/**
 * Render input password
 * @param values
 * @param handleChange
 * @param touched
 * @param errors
 * @param handleBlur
 * @param fieldName
 * @param placeholder
 */
export const RenderInputPassword = (props: {
  values: any;
  handleChange: any;
  touched: any;
  errors: any;
  handleBlur: any;
  name: string;
  label: string;
  show: boolean;
  setShow: any;
  hideLabel?: boolean;
  autoComplete?: string;
}) => {
  const {
    values,
    handleChange,
    touched,
    errors,
    handleBlur,
    name,
    label,
    show,
    setShow,
    hideLabel,
    autoComplete,
  } = props;

  return (
    <CFormGroup>
      {!hideLabel && <CLabel>{label}</CLabel>}
      <div style={{ position: 'relative' }}>
        <CInput
          name={name}
          type={show ? 'text' : 'password'}
          value={values?.[name]}
          onChange={(e) => {
            const target = e.target as HTMLTextAreaElement;
            if (!target.value.includes(' ')) handleChange(e);
          }}
          placeholder={label}
          invalid={values?.[name] !== '' && touched?.[name] && !!errors?.[name]}
          maxLength={16}
          onBlur={handleBlur}
          autoComplete={autoComplete}
          style={{ paddingRight: 48 }}
        />
        <div style={{ position: 'absolute', right: 0, top: 10 }}>{renderIconPassword(show, () => setShow(!show))}</div>
        <CInvalidFeedback>{errors?.[name]}</CInvalidFeedback>
      </div>
    </CFormGroup>
  );
};
