import { useMemo } from 'react';

import { useDispatch, useSelector, shallowEqual } from 'react-redux';
import { RootState } from '../index';

import {
  resetAppointmentData,
  setStep,
  updateAppointmentData,
  updateQuestions,
  updateStatistics,
  updateTopics,
} from './actions';
import { AppointmentsStatistics } from 'types/appointments';

export default function useAppointment() {
  const dispatch = useDispatch();
  const appointmentReducer = useSelector((state: RootState) => state.appointmentReducer, shallowEqual);

  return useMemo(
    () => ({
      ...appointmentReducer,
      setStep: (step: number) => dispatch(setStep(step)),
      updateTopics: (topics: any) => dispatch(updateTopics(topics)),
      updateQuestions: (questions: any) => dispatch(updateQuestions(questions)),
      updateAppointmentData: (appointmentData: any) => dispatch(updateAppointmentData(appointmentData)),
      updateStatistics: (statistics: AppointmentsStatistics) => dispatch(updateStatistics(statistics)),
      resetAppointmentData: () => dispatch(resetAppointmentData()),
    }),
    [dispatch, appointmentReducer],
  );
}
