import React from 'react';
import './HBButtonActivity.scss';

interface Props {
  dark?: boolean;
  icon: string;
  handleClick: any;
}

const HBButtonActivity: React.FC<Props> = (props) => {
  const { dark, icon, children, handleClick } = props;

  return (
    <div className={`hb-button-activity${dark ? ' dark' : ''}`} onClick={handleClick}>
      <div className="d-flex align-items-center" style={{ width: 'calc(100% - 30px)' }}>
        <div className="icon">
          <i className={`hb-icon-${icon}`} />
        </div>
        <div className="content">{children}</div>
      </div>
      <div className="arrow">
        <i className="hb-icon-arrow-right" />
      </div>
    </div>
  );
};

export default HBButtonActivity;
