import React from 'react';
import { HBButtonSmall } from 'hbBaseClass';

import './ButtonOptions.scss';

type Option = {
  label: string;
  value: string | number;
};

type PropTypes = {
  className?: string;
  options: Option[];
  value?: any;
  onClick?: Function;
  disabled?: boolean;
};

const ButtonOptions = (props: PropTypes) => {
  const { className = '', value, options = [], disabled, onClick = () => {} } = props;

  /**
   * Handle click button
   *
   * @param optionValue Button's value
   */
  const handleClick = (optionValue: any) => {
    onClick && onClick(optionValue);
  };

  return (
    <div className={`ButtonOptions-container ${className}`}>
      {options.map((option, index) => (
        <HBButtonSmall
          disabled={disabled}
          key={index}
          type="button"
          color="petrol"
          light={option.value === value}
          onClick={() => handleClick(option.value)}
        >
          {option.label}
        </HBButtonSmall>
      ))}
    </div>
  );
};

export default ButtonOptions;
