import React, { useCallback, useState } from 'react';
import './HBFocusInput.scss';

interface Props extends React.InputHTMLAttributes<HTMLInputElement> {
  error?: any;
  invalid?: boolean;
  color?: 'petrol' | 'gray' | 'white';
}

const HBFocusInput: React.FC<Props> = (props) => {
  let { className } = props;

  const [isFocusing, setIsFocusing] = useState(false);

  const {
    type = 'text',
    name = '',
    error,
    value,
    onFocus,
    onBlur,
    color = '',
    disabled,
    invalid: oldInvalid,
    ...rest
  } = props;

  const isPassword = type === 'password';

  const [passwordShown, setPasswordShown] = useState(!isPassword);

  /**
   * When input is focus, save status focus to this field name
   */
  const handleFocus = useCallback((e: any) => {
    setIsFocusing(true);
    onFocus?.(e);
  }, []);

  /**
   * Reset status focus when blur field
   */
  const handleBlur = useCallback((e: any) => {
    setIsFocusing(false);
    onBlur?.(e);
  }, []);

  const invalid = !isFocusing && oldInvalid;
  const valid = !isFocusing && value;

  let iconClassName = '';
  let iconClassName2 = '';

  if (invalid) {
    iconClassName = 'icon-invalid';
    iconClassName2 = ' hb-icon-cross-2';
  } else if (valid) {
    iconClassName = 'icon-valid';
    iconClassName2 = ' hb-icon-check-2';
  }

  if (isPassword)
    iconClassName = `${iconClassName} cursor-pointer ${passwordShown ? 'hb-icon-eye-slash' : 'hb-icon-eye'}`;
  else iconClassName = `${iconClassName}${iconClassName2}`;

  className = `${className ? ` ${className}` : ''}${color ? ` ${color}` : ''}`;

  return (
    <div className={`HBFocusInput${className}`}>
      <div className="HBFocusInput-wrapper">
        <input
          name={name}
          onFocus={handleFocus}
          onBlur={handleBlur}
          type={isPassword ? (passwordShown ? 'text' : 'password') : type}
          value={value}
          disabled={disabled}
          {...rest}
          className={invalid ? 'is-invalid' : ''}
        />
        {!disabled && iconClassName && (
          <i
            className={`${iconClassName} input-icon`}
            onClick={() => (isPassword ? setPasswordShown(!passwordShown) : {})}
          />
        )}
      </div>
      {invalid && <div className="HBFocusInput-invalid">{error || ''}</div>}
    </div>
  );
};

export default HBFocusInput;
