import React from 'react';

import InputRadioVertical, { InputRadioVerticalTypes } from './InputRadioVertical';

interface Option extends InputRadioVerticalTypes {
  label: string;
  value: string | number;
  icon?: string;
  checked?: boolean;
}

interface PropTypes {
  className?: string;
  options: Option[];
  [key: string]: any;
}

const ListInputRadiosVertical = (props: PropTypes) => {
  const { className = '', options = [], value, onChange = () => {}, ...rest } = props;

  return (
    <div className={`ListInputRadiosVertical-container ${className}`} {...rest}>
      {options.map((option, index) => (
        <InputRadioVertical {...option} key={index} checked={value === option.value} onChange={onChange} />
      ))}
    </div>
  );
};

export default ListInputRadiosVertical;
