import React, { useMemo } from 'react';
import { CInputCheckbox } from '@coreui/react';
import InputRadioCustom from './InputRadioCustom';

import './InputRadio.scss';

export interface InputRadioTypes extends CInputCheckbox {
  className?: string;
  label: string;
}

const InputRadio = (props: InputRadioTypes) => {
  const { className = '', label, value, checked, ...rest } = props;

  // Generate input radio id
  const inputRadioId = useMemo(() => `InputRadio-id_${value}`, [value]);

  return (
    <label className={`InputRadio-container ${className} ${checked && 'checked'}`} htmlFor={inputRadioId}>
      <InputRadioCustom id={inputRadioId} value={value} checked={checked} {...rest} />
      <span className="InputRadio-lb">{label}</span>
    </label>
  );
};

export default InputRadio;
