import {
  APPOINTMENT_RESET,
  APPOINTMENT_SET_STEP,
  APPOINTMENT_UPDATE_APPOINTMENT_DATA,
  APPOINTMENT_UPDATE_QUESTIONS,
  APPOINTMENT_UPDATE_STATISTICS,
  APPOINTMENT_UPDATE_TOPICS,
} from '../actionTypes';
import { AppointmentsStatistics } from 'types/appointments';

/**
 * Set step book appointment
 *
 * @param step Step value
 */
export const setStep = (step: number) => {
  return { type: APPOINTMENT_SET_STEP, payload: step };
};

/**
 * Update topics
 *
 * @param topics Topics
 */
export const updateTopics = (topics: any) => {
  return { type: APPOINTMENT_UPDATE_TOPICS, payload: topics };
};

/**
 * Update questions
 *
 * @param questions Questions
 */
export const updateQuestions = (questions: number) => {
  return { type: APPOINTMENT_UPDATE_QUESTIONS, payload: questions };
};

/**
 * Update appointment data
 *
 * @param appointmentData Appointment data (maybe one of appointmentData's properties in reducer)
 */
export const updateAppointmentData = (appointmentData: any) => {
  return { type: APPOINTMENT_UPDATE_APPOINTMENT_DATA, payload: { ...appointmentData } };
};

/**
 * Update statistics
 *
 * @param statistics Statistics data
 */
export const updateStatistics = (statistics: AppointmentsStatistics) => {
  return { type: APPOINTMENT_UPDATE_STATISTICS, payload: statistics };
};

/**
 * Reset book an appointment data
 */
export const resetAppointmentData = () => {
  return { type: APPOINTMENT_RESET };
};
