import React from 'react';
import { CSelect } from '@coreui/react';

import FormGroupCommon, { FormGroupCommonTypes } from './FormGroupCommon';

import './FormGroup.scss';

type Option = {
  value?: string | number;
  label: string;
};

interface FormGroupSelectTypes extends FormGroupCommonTypes {
  options: Option[];
}

const FormGroupSelect = (props: FormGroupSelectTypes & CSelect) => {
  const { className = '', options = [], onChange = () => {}, ...rest } = props;

  return (
    <FormGroupCommon className={`FormGroupSelect-container ${className}`} {...rest}>
      <CSelect onChange={onChange} {...rest}>
        {options.map((option, index) => (
          <option key={index} value={option.value}>
            {option.label}
          </option>
        ))}
      </CSelect>
    </FormGroupCommon>
  );
};

export default FormGroupSelect;
