import React, { forwardRef } from 'react';
import { CInput } from '@coreui/react';
import DatePicker, { ReactDatePickerProps } from 'react-datepicker';

import FormGroupCommon, { FormGroupCommonTypes } from './FormGroupCommon';

import 'react-datepicker/dist/react-datepicker.css';
import './FormGroup.scss';

interface PropTypes extends FormGroupCommonTypes, Omit<CInput, 'selected' | 'onChange'> {}

/**
 * Custom calendar icon
 */
const IconCalendar = forwardRef(function CustomDateInput({ onClick }: any, ref: any) {
  return (
    <div className="FormGroupDatePicker-btn" onClick={onClick} ref={ref}>
      <i className="hb-icon-calendar-date" />
    </div>
  );
});

const FormGroupDatePicker = (props: PropTypes & ReactDatePickerProps) => {
  const {
    className = '',
    disabled,
    minDate,
    maxDate,
    selected,
    showYearDropdown,
    showMonthDropdown,
    onChange = () => {},
    ...rest
  } = props;

  return (
    <FormGroupCommon className={`FormGroupDatePicker-area ${className}`} {...rest}>
      <div className="FormGroupDatePicker-container">
        <CInput type="text" autoComplete="off" disabled={disabled} {...rest} />
        <div className="hb-datepicker">
          <DatePicker
            customInput={<IconCalendar />}
            yearDropdownItemNumber={40}
            showYearDropdown={showYearDropdown}
            showMonthDropdown={showMonthDropdown}
            scrollableYearDropdown
            adjustDateOnChange
            popperPlacement="bottom-end"
            selected={selected}
            minDate={minDate}
            maxDate={maxDate}
            onChange={onChange}
            {...rest}
          />
        </div>
      </div>
    </FormGroupCommon>
  );
};

export default FormGroupDatePicker;
