import React, { useMemo } from 'react';
import { CInputCheckbox } from '@coreui/react';
import InputRadioCustom from './InputRadioCustom';

import './InputRadio.scss';

export interface InputRadioVerticalTypes extends CInputCheckbox {
  className?: string;
  label: string;
  icon?: string;
}

const InputRadioVertical = (props: InputRadioVerticalTypes) => {
  const { className = '', label, icon = '', value, checked, ...rest } = props;

  // Generate input radio id
  const inputRadioId = useMemo(() => `InputRadioVertical-id_${value}`, [value]);

  return (
    <label className={`InputRadioVertical-container ${className} ${checked && 'checked'}`} htmlFor={inputRadioId}>
      {icon && <span className={`hb-icon-${icon} InputRadioVertical-icon`} />}
      <span className="InputRadioVertical-lb">{label}</span>
      <InputRadioCustom id={inputRadioId} value={value} checked={checked} {...rest} />
    </label>
  );
};

export default InputRadioVertical;
