import React from 'react';

interface Props {
  className?: string;
  size: 'lg' | 'md' | 'sm';
  render: any;
}

const mappingSize = { lg: 80, md: 48, sm: 36 };

const Frame: React.FC<Props> = (props) => {
  const { className = '', size, render } = props;

  return (
    <div className={className} style={{ marginTop: 14, marginBottom: 20, display: 'flex', width: '100%' }}>
      <div style={{ width: '100%', height: mappingSize[size] }}>{render}</div>
    </div>
  );
};

export default Frame;
