import React from 'react';
import { CInput } from '@coreui/react';

import FormGroupCommon, { FormGroupCommonTypes } from './FormGroupCommon';

import './FormGroup.scss';

const FormGroupInput = (props: FormGroupCommonTypes & CInput) => {
  const { btnLabel, btnLabelOnClick = () => {}, autoComplete = 'off', msgError = '', ...rest } = props;

  return (
    <FormGroupCommon btnLabel={btnLabel} btnLabelOnClick={btnLabelOnClick} msgError={msgError} {...rest}>
      <CInput autoComplete={autoComplete} {...rest} />
    </FormGroupCommon>
  );
};

export default FormGroupInput;
